import {
  Component,
  Injectable,
  Inject,
  ElementRef,
  Host,
  AfterViewInit
} from '@angular/core';

import { OCarouselContainerComponent } from './o-carousel-container.component';



export class OCarouselComponent implements AfterViewInit {

  private elm: HTMLElement;
  private swiper: OCarouselContainerComponent;

  constructor(
     elementRef: ElementRef,
      swiper: OCarouselContainerComponent
  ) {
    this.elm = elementRef.nativeElement;
    this.swiper = swiper;
  }

  public ngAfterViewInit() {
    this.elm.classList.add('swiper-slide');
    this.swiper.update();
  }

static decorators: DecoratorInvocation[] = [
{ type: Injectable },
{ type: Component, args: [{
  selector: 'o-carousel-slide',
  template: '<div><ng-content></ng-content></div>'
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ElementRef, decorators: [{ type: Inject, args: [ElementRef, ] }, ]},
{type: OCarouselContainerComponent, decorators: [{ type: Host }, { type: Inject, args: [OCarouselContainerComponent, ] }, ]},
];
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
